body {
  margin: 0;
  background: #E7F6FF;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  font-family: 'Inter', system-ui;
}

.wrapper{
  background: #FFFFFF;

  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;

}

header{
border-bottom: 1px solid #EAEAEA;
  h3, p {
    margin: 0
  }
img {margin-right: 15px}

}

.content {
  h1{ margin: 0;}
}



.favorite{
  position: absolute;
  cursor: pointer;
}


.search-block{
  border: 1px solid #F3F3F3;
  border-radius: 10px;
  padding: 0 15px;
  position: relative;


  .clear{
    position: absolute;
    right: 0;
    width: 19px;
    height: 19px;
    top: 13px;
    right: 9px;
  }

  input {
    border: 0;
    padding: 13px;
    font-size: 16px;
    width: 200px;
  }

}





.cartTotalBlock{

ul{
  margin-bottom: 30px !important;
  display: block;

  li{
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;

    div{
      flex: 1;
      height: 1px;
      border-bottom: 1px dashed #DFDFDF;
      position: relative;
      top: -4px;
      margin: 0 7px;
    }
  }
}
  .greenButton{
    position: relative;

    &:disabled{
      animation: button-loading 0.9s ease-in-out infinite;
    }

    &:hover{
      img{
        transform: translateX(5px);
      }
    }

    img{
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.2s ease-in-out;
    }
  }
}

.greenButton{
    width: 100%;
    height: 55px;
    background: #9DD558;
    border-radius: 18px;
    border: 0;
    color: #FFFFFF;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.15s ease-in-out;

  &:disabled{
    background-color: #bebebe !important;
    cursor: default;
  }

    &:hover{
      background: lighten(#9DD558, $amount: 5%);

      &:active{
        background: darken(#9DD558, $amount: 5%);
      }
    }
}

.cartEmpty{
  text-align: center;

  p{
    width: 280px;
    line-height: 24px;
  }

  .greenButton{
    width: 245px;
    margin-top: 15px;

    &:hover{
      img {
        transform: rotate(180deg) translateX(3px);
      }
    }
  img{
    position: relative;
    top: 1px;
    transform: rotate(180deg);
    margin-right: 10px;
    transition: transform 0.15s ease-in-out;
    }
  }
}


.cartItem {
  border: 1px solid rgba(243, 243, 243, 1);
  border-radius: 20px;
  overflow: hidden;
  padding: 20px;

.cartItemImg {
  width: 70px;
  height: 70px;
  background-size: contain;
  margin-right: 20px;
  background-repeat: no-repeat;
  background-position: 0 5px;
}

p {
  font-size: 16px;
  margin: 0;
}
  b{ font-size: 14px
  }
}

.removeBtn{
  opacity: 0.5;
  cursor: pointer;
  transition: opacity 0.15s ease-in-out;
  &:hover{
    opacity: 1;
  }
}

@keyframes button-loading {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}